(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .directive('range', drctv);

  /** @ngInject */
  function drctv() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/range/range.html',
      scope: {
        min: '=',
        max: '=',
        model: '=',
        disabled: '=',
        change: '&'
      },
      link: linkFunc,
      controller: ctrl,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;

      var update = _.debounce(vm.change, 500);

      watcher = scope.$watch('vm.model', function () {
        el[0].children[0].style.backgroundSize = ((vm.model - vm.min) / (vm.max - vm.min) * 100) + '% 100%';
        el[0].children[0].value = vm.model;

        update();
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function ctrl() {
      var vm = this;

      return vm;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

