(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Micro5Controller', Micro5Controller);

  /** @ngInject */
  function Micro5Controller(MicroService, $stateParams, $http, $state, configUrls, environment) {
    var vm = this;

    vm.policyTemplate = MicroService.policyTemplateBill
    vm.PolNo = MicroService.PolNo

    vm.policyNumberBP = '12312312'
    vm.policyCover = '$' + MicroService.Cover

    vm.loading = false
    vm.checkbox = false
    vm.loaded = false

    vm.month = 1
    vm.premiums = 1

    vm.loadPolicy = function (PolNo) {
      if(vm.checkbox) {
        if(PolNo) {
          vm.getPolicy(PolNo)
          console.log('load')
        } else {
          console.log('new')
          vm.newPolicy()
        }
      }
    }

    vm.newPolicy = function () {
      //vm.loading = true
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/newbusiness/newpolicy',
        url: configUrls[environment].createPolicy,
        data: angular.toJson(vm.policyTemplate)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }

      $http(req)
        .then(function (response) {
          $state.go('micro-5', {'policyNumberBP': response.data.PolicyID});
          MicroService.policyNumberBP = response.data.PolicyID
        }, function () {
      });
    }

    vm.getPolicy = function (PolNo) {
      //vm.loading = true
      var req = {
        method: 'GET',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + PolNo
        url: configUrls[environment].enquirePolicy +  PolNo,
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function (response) {
          vm.loading = false
          vm.loaded = true
          if(response.data.Cont.Risks["1"].RiskBody.Options[0].SumInsured < 900) {
            vm.cover = 900
          } else {
            vm.cover = response.data.Cont.Risks["1"].RiskBody.Options[0].SumInsured
          }
        }, function () {
      });
    }    

    vm.TopUpTemplate = {
      "PolicyID": MicroService.policyNumberBP,
      "ReceiptDate": "2021-08-18",
      "Amount": 0.3,
      "Reference": "TopUp1",
      "Data": [{"Plan":"Plan2"}]
    }

    vm.addCover = function () {
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/service/topuppremium',
        url: configUrls[environment].topupPremium,
        data: angular.toJson(vm.TopUpTemplate)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function (response) {
          vm.month = vm.month + 1;
          vm.premiums = vm.premiums + 1;
          vm.getPolicy(MicroService.policyNumberBP);
        }, function () {
      });
    }

    function init() {
      if($stateParams.policyNumberBP=="new") {
        vm.checkbox = false
      } else if($stateParams.policyNumberBP) {
        MicroService.setPolNo($stateParams.policyNumberBP)
        vm.checkbox = true
        vm.PolNo = $stateParams.policyNumberBP
        vm.loadPolicy(vm.PolNo)
      }
    }

    init();

  }
})();
(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Micro4Controller', Micro4Controller);

  /** @ngInject */
  function Micro4Controller(MicroService, $state, $http, $stateParams, PurchaseService, configUrls, environment) {
    var vm = this;

    vm.policyTemplate2 = MicroService.policyTemplate2
    vm.PolNo = MicroService.PolNo
    vm.PolNoCI = MicroService.PolNoCI
    vm.netPrice = 200
    vm.discount = 0

    vm.checkbox = false
    vm.loaded = false

    vm.mileage = 1
    vm.points = 1
    vm.renewalDate = moment().add(1, 'months').format("DD-MMM YYYY")

    vm.SparkTemplate = {
        "Inputs": {
            "Data": '[{\"Points\":99,\"Distance\":1000}]',
            "Plan": "Comprehensive"
        }
    }

    vm.appyPremiumTemplate =
    {
      "PolicyID": "RideShare-21-040900",
      "ReceiptDate": "2021-12-20T15:04:05Z",
      "Amount": 19.1,
      "Reference": "ref0993",
      "Data": [
        {
            "Points": "99",
            "Distance": "1000"
        }
      ]
    }

    vm.price = function () {

      var settings = {
        method: 'GET',
        url: configUrls[environment].keyCloakTokenUrl + '?apiKey=' + configUrls[environment].keyCloakApiKey + '&client=' + configUrls[environment].keyCloakPFClient,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };

      $http(settings).then(function (response) {
        console.log(response);
        return response
      }).then(function (tokenRes) {

        // ----- Existing code here ----//
        vm.SparkTemplate.Inputs.Data = '[{\"Points\":' + vm.points + ',\"Distance\":' + vm.mileage + '}]'
        var req = {
          method: 'POST',
          // url: 'https://excel.sonic.dev.coherent.com.hk/api/v1/product/coherent/engines/Execute/PPP',
          url: configUrls[environment].PFUrl + 'PPP',          
          headers: {
            authorization: 'Bearer ' + tokenRes.data.access_token
          },
          data: angular.toJson(vm.SparkTemplate),
          // headers: {
          //   'SecretKey': '762f5692-5a8b-4f8e-a09e-a27a277c0c56',
          //   'x-tenant-name': 'coherent'
          // }
        };
        $http(req)
          .then(function (response) {
            console.log(response)
            vm.discount = Math.round(response.data.data.Outputs.BasePremium - response.data.data.Outputs.InstallmentPremium)
            vm.netPrice = Math.round(response.data.data.Outputs.InstallmentPremium)
          }, function () {
        });
        // ----- Existing code here ----//

      });
    }

    vm.renew = function () {
      vm.appyPremiumTemplate.PolicyID = vm.PolNoCI
      vm.appyPremiumTemplate.Data[0].Points = vm.points
      vm.appyPremiumTemplate.Data[0].Distance = vm.mileage
      vm.appyPremiumTemplate.ReceiptDate = moment(vm.renewalDate).format("YYYY-MM-DD")
      vm.appyPremiumTemplate.Amount = vm.netPrice
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/service/applypremium',
        url: configUrls[environment].applyPremium,
        data: angular.toJson(vm.appyPremiumTemplate)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }

      $http(req)
        .then(function (response) {
          vm.renewalDate = moment(vm.renewalDate).add(1, 'months').format("DD-MMM YYYY")
          vm.mileage = 1//Math.round(Math.random() * 20000)
          vm.points = 1//Math.round(Math.random() * 100)
          vm.price()
        }, function () {
      });
    }

    vm.newPolicy = function () {
      //vm.loading = true
      var req = {
        method: 'POST',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/newbusiness/newpolicy',
        url: configUrls[environment].createPolicy,
        data: angular.toJson(vm.policyTemplate2)
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }

      $http(req)
        .then(function (response) {
          $state.go('micro-4', {'policyNumberCI': response.data.PolicyID});
        }, function () {
      });
    }

    vm.loadPolicy = function (PolNoCI) {
      if(vm.checkbox) {
        if(PolNoCI) {
          vm.getPolicy(PolNoCI)
          console.log('load')
        } else {
          console.log('new')
          vm.newPolicy()
        }
      }
    }

    vm.getPolicy = function (PolNoCI) {
      //vm.loading = true
      var req = {
        method: 'GET',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + PolNoCI
        url: configUrls[environment].enquirePolicy +  PolNoCI
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function (response) {
          vm.loading = false
          vm.loaded = true
          vm.cover = response.data.Cont.Risks["1"].RiskBody.Options[0].SumInsured
        }, function () {
      });
    }

    function init() {
      if($stateParams.policyNumberCI=="new") {
        vm.checkbox = false
      } else if($stateParams.policyNumberCI) {
        MicroService.setPolNoCI($stateParams.policyNumberCI)
        vm.checkbox = true
        vm.PolNoCI = $stateParams.policyNumberCI
        vm.loadPolicy(vm.PolNoCI)
        vm.price()
      }
    }

    init();

  }
})();
