(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('PolicyLoginController', PolicyLoginController);

  /** @ngInject */
  function PolicyLoginController($http, PolicyService, $stateParams, $state, configUrls, environment) {
    var vm = this;

    vm.signIn = function () {
      var req = {
        method: 'GET',
        // url: 'https://general.sonic.dev.coherent.com.hk/general/v2/enquiry/policy/' + vm.number
        url: configUrls[environment].enquirePolicy + vm.number,
      };
      if (configUrls[environment].isAuthorizationNeeded) {
        req.headers = {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      }
      
      $http(req)
        .then(function () {
          $state.go('policy-1', {'policyNumber': vm.number});
        }, function () {
        });
    }

    if(!_.isEmpty(PolicyService.policyDetails) || !_.isEmpty(PolicyService.polNo) ) {
      $state.go('policy-1', {'policyNumber': PolicyService.polNo});
    }
  }
})();

