      vm.cancellationBody.PolicyID = PolicyService.polNo
      vm.cancellationBody.TranDate = moment(vm.cancelDate).format("YYYY-MM-DD")
      vm.cancellationBody.CancelDate = moment(vm.cancelDate).format("YYYY-MM-DD")
